<template>
    <div class="flex flex-col gap-4 px-10 py-4 mb-6 text-xl shadow-round bg-dark_blue_1">        
        <div class="flex flex-col gap-3 text-xl md:flex-row">
            <h2 class="text-left md:w-1/12">Destino: </h2>
            <span class="px-4 text-left border-b md:w-2/5 capitalize">{{ request.destination }}</span>
        </div>
        <div class="flex gap-3 text-xl">
            <h2 class="text-left md:w-1/12">Producto: </h2>
            <span class="px-4 text-left border-b md:w-2/5 capitalize">{{ request.product }}</span>
        </div>
        <component :is="components[request.type]" :info="request" @updInfo="updRequest"></component>                
    </div>    
    
</template>
<script>
import HomeRequest from '@/components/request/home_request'
import HotelRequest from '@/components/request/hotel_request'
import CarRequest from '@/components/request/car_request'

export default {    
    name:'GeneralRequest',
    props: {
        request: {
            required: true,
        }
    },
    components:{
        HomeRequest,
        HotelRequest,
        CarRequest,
    },    
    data() {
        return {
            components: {
                home: 'HomeRequest',
                hotel: 'HotelRequest',
                car: 'CarRequest',
            },
        }
    },
    methods: {
        updRequest(msg) {            
            const message = {
                destination: this.request.destination,
                product: this.request.product,
                is_hotel: (this.request.type === 'hotel'),
                ...msg
            }
            this.$emit('updInfo', message)
        }
    } 
}
</script>
