<template>
    <div class="flex flex-col flex-wrap shadow-round bg-dark_blue_1 text-xl px-3 md:px-10 py-4 mb-6
        md:flex-row">
        <div class="flex flex-col gap-3 items-start
        md:w-3/12">
            <span class="capitalize"><b>Pais: </b> {{ info.country }}</span>
            <span v-if="(info.pole)"><b>Polo: </b> {{ info.pole }}</span>
            <span class="capitalize" v-if="(info.product != '')"><b>Producto:</b> {{ info.product }}</span>
        </div>
        <div class="flex flex-col gap-3 items-start py-3 px-8 border my-2
        md:w-9/12 md:m-0">
            <div class="flex gap-x-3 flex-col md:flex-row">
            <b class="capitalize" v-if="(ishotel)">{{ info.product_name }}</b> 
            <span class="text-base self-center" v-if="(ishotel)">({{ formatDate }})</span>
            </div>                                    
            <template v-if="(info.is_home_request)">
            <div class="flex flex-col w-full gap-x-3 gap-y-6
            md:flex-row">                      
                <div class="flex flex-col gap-y-1 items-start
                md:w-5/12">
                <small class="text ml-3">Fecha</small>                
                </div>
                <number-imput v-if="(info.product == 'Hoteles')" class="md:w-4/12" :title="'Cantidad de personas'" 
                :min="1" 
                :value="info.num_person" 
                :id="1" 
                @updVal="updPerson"></number-imput>
            </div>
            </template>
            <template v-if="(ishotel)">
                <article class="flex flex-col text-left w-full gap-y-2">
                    <section class="flex gap-x-4 text-base pb-2">
                        <span><b>Plan:</b> {{ info.category }}</span>
                    </section>
                    <section class="flex gap-x-4 text-base pb-2">
                        <span><b>Habitaciones:</b> {{ info.num_rooms }}</span>
                        <span><b>Noches:</b> {{ numNights }}</span>
                        <span><b>Personas:</b> {{ info.num_person }}</span>
                    </section>
                    <section class="flex gap-x-3 text-base" v-for="(item, index) in info.rooms" :key="index">
                        <span >{{ item.type }}:</span> 
                        <span>{{ item.comb }}</span>
                    </section>
                </article>
            </template>
            <span class="self-end text-2xl font-bold ml-auto" v-if="(info.price > 0)">Precio total:<span class="text-orange_1"> ${{ info.price }}</span></span>
        </div>                
    </div>
</template>
<script>

import moment from 'moment'

export default {
    name: 'HotelRequest',   
    props: {
        info: {
            default: {}
        }
    },
    data() {        
        return {    
            rooms: ['Simple', 'Doble', 'Triple'],
            dateRange: null,  
            num_nights: 1,            
            request_info: {type: 'home', handler: {}},
        }
    },  
    computed: {                
        ishotel() {
            return (this.info.type == 'hotel')
        },
        persons() {
            const person = (this.info.product == 'hotel') ? this.info.num_person : person
            return person
        },
        formatDate() {
            const dates = this.info.date.split('/')
            // let from, to
            let text_date
            if(dates.length > 1) {
                // from = moment(dates[0])        
                // to = moment(dates[1])                
                text_date = dates[0] + ' - ' + dates[1]
                // text_date = from.format('DD/MM/YYYY') + ' - ' + to.format('DD/MM/YYYY')
            } else {
                text_date = moment(dates[0]).format('DD/MM/YYYY')
            }
            return text_date
        },
        numNights() {
            const dates = this.info.date.split('/')
            let num_nights = 1
            let from, to
            if(dates.length > 1) {
                from = moment(dates[0], 'DD/MM/YYYY')        
                to = moment(dates[1], 'DD/MM/YYYY')
                num_nights = to.diff(from, 'days')
            }
            return num_nights
        }
    },    
}
</script>
