<template>
    <div class="flex flex-col flex-wrap shadow-round bg-dark_blue_1 text-xl px-3 md:px-10 py-4 mb-6 md:flex-row">
        <div class="flex flex-col gap-3 items-start md:w-3/12">
            <span class="capitalize"><b>Pais: </b> {{ info.country }}</span>
            <span v-if="(info.pole)"><b>Polo: </b> {{ info.pole }}</span>
            <span class="capitalize" v-if="(info.product != '')"><b>Producto:</b> {{ info.product }}</span>
        </div>
        <section class="flex flex-col gap-4 items-start py-3 px-4 border my-2 md:w-9/12 md:m-0">           
            <div class="flex gap-x-8">
                <div class="text-base pb-2">
                    <span class="leading-8"><b>Fecha: </b> ({{ info.date }}) </span>
                </div>
                <div class="flex gap-x-4 text-base pb-2">
                    <span class="leading-8"><b>Auto:</b> {{ info.car_category.alias }}</span>
                </div>
                <div class="flex gap-x-4 text-base pb-2">
                    <span class="leading-8"><b>Motor:</b> {{ info.car_engine.alias }}</span>
                </div>
            </div>

            <!-- Licencia de conduccion-->
            <label for="" class="text-">
                <input type="checkbox" v-model="licence">
                Cuenta con licencia de conducción por periodo mayor a 2 años
            </label>

            <!-- Lugar -->
            <div class="flex md:flex-row flex-col gap-3 w-full">
                <div class="flex flex-col gap-y-1 text-gray-700 w-3/6">
                    <Multiselect
                        mode="single"
                        v-model="car_pickup_place"
                        :searchable="true"
                        :options="car_pickup_places"
                        :classes="select_style_opt"
                        placeholder="Seleccione Lugar de recogida"
                        @change="updInfo"
                    />                
                    <small class="px-3 py-1 border-1 border-white text-white text-left" v-html="getTimePickUp"></small>                
                </div>
                <div class="flex flex-col gap-y-1 text-gray-700 w-3/6">
                    <Multiselect
                        mode="single"
                        v-model="car_dropoff_place"
                        :searchable="true"
                        :options="car_pickup_places"
                        :classes="select_style_opt"
                        placeholder="Seleccione Lugar de entrega"
                        @change="updInfo"
                    />            
                    <small class="px-3 py-1 border-1 border-white text-white text-left" v-html="getTimePickOff"></small>                
                </div>                
            </div>

            <small class="w-full py-1 px-4 bg-light_blue_1 text-dark_blue_1 text-sm text-left" v-show="checkFee">
                <i class="fa fa-warning"></i>
                Tarifa por recogida en aeropuerto: $20
            </small>
            
        </section>                
            <span class="self-end text-2xl font-bold ml-auto" v-if="(info.price > 0)">Precio total:<span class="text-orange_1"> ${{ getPrice }}</span></span>
    </div>
</template>
<script>

// import moment from 'moment'
import Multiselect from '@vueform/multiselect';
import '@vueform/multiselect/themes/default.css';
import CarService from '@/services/car.service';


export default {
    name: 'CarRequest',
    components:{
      Multiselect,
    },   
    props: {
        info: {
            default: {}
        }
    },
    data() {        
        return {                
            formattedDate: '',
            dateRange: null,
            car_pickup_places: [],
            car_pickup_place: '',  
            car_dropoff_place: '', 
            licence: false,      
            // num_nights: 1,            
            request_info: {type: 'car', handler: {}},
            select_style_opt: {
                container: 'relative mx-auto w-full flex items-center justify-end box-border cursor-pointer border border-white bg-dark_blue_1 text-base leading-snug outline-none',
                search: 'w-full absolute inset-0 outline-none focus:ring-0 appearance-none box-border border-8 border-transparent text-base font-sans bg-dark_blue_1 pl-2 rtl:pl-0 rtl:pr-3.5',                                                        
                placeholder: 'flex items-center w-full h-full absolute left-0 top-0 pointer-events-none bg-transparent leading-snug pl-3.5 text-white rtl:left-auto rtl:right-0 rtl:pl-0 rtl:pr-3.5',
                singleLabelText: 'text-white text-left',
                option: 'text-slate-600',                
                optionSelected: 'bg-light_gray_1',                
            },
        }
    },
    async created() {
        this.car_pickup_places = await CarService.getPlaces();
        // console.log(this.car_pickup_places)
    },  
    computed: {
        getCar(){
            if(!this.info.car_category && !this.info.car_engine) return;

            return {
                car_category: this.info.car_category,
                car_engine:  this.info.car_engine,
            }
        },
        getTimePickUp() {
            let time = '';
            if(this.car_pickup_place) {                                
                time = this.car_pickup_places.find(e => this.car_pickup_place == e.value);
                time = (time) ? `Horario: ${time.pickup} - ${time.dropoff}` : '';
            }
            return time;
        },
        getTimePickOff() {
            let time = '';
            if(this.car_dropoff_place) {                                
                time = this.car_pickup_places.find(e => this.car_dropoff_place == e.value);
                time = (time) ? `Horario: ${time.pickup} - ${time.dropoff}` : '';
            }
            return time;
        },
        getPrice() {
            let totalPrice = this.info.price;
            if(this.car_pickup_place) {                                
                if(this.checkFeePrice())
                    totalPrice += 20;
            }
            return totalPrice;
        }, 
        checkFee() {
            return this.checkFeePrice();
        }       
    },
    methods:{
        updInfo(){
            this.$nextTick(() => {
                this.$emit('updInfo', {
                    car_pickup_place_id: this.car_pickup_place,
                    car_dropoff_place_id: this.car_dropoff_place,                    
                    licence: this.licence,
                    fee: this.checkFeePrice()
                });
            });
        },   
        checkFeePrice() {
            const pickUpPlace = this.car_pickup_places.find(e => this.car_pickup_place == e.value);
            if(this.car_pickup_place) {   
                if(pickUpPlace.place.toLowerCase() === 'aeropuerto'){
                    return true;               
                }
            }
            return false;
        }     
    }
}
</script>
<style scoped>
.multiselect__input {
    @apply text-white bg-dark_blue_1;
}
/* /deep/ .multiselect-wrapper {
    color: white;
} */
</style>