<template>
    <div class="flex flex-col items-center gap-3 p-4 border md:flex-row md:w-2/4">        
        <!-- Remesas -->
        <div class="flex w-4/12 gap-3" v-if="(isProduct('Remesas'))">
            <h2>Cantidad: </h2>
            <input class="w-2/4 p-1 px-2 text-blue_1" type="text" v-model="inputVal">
        </div>
        <!-- Carros  -->
        <template  v-if="(isProduct('Carros') || isProduct('Hoteles'))">
            <Datepicker 
                class="datepicker-container clear"
                v-model="dateRange" 
                :format="'dd/MM/yyyy'"
                range multi-calendars 
                auto-apply
                :enable-time-picker="false"
                :close-on-auto-apply="true"
                :placeholder="placeholder"
                @change="handleDate"
            />
        </template>
        <h2 class="ml-6" v-if="!isProduct('Hoteles')">{{ (isProduct('Remesas')) ? 'Precio a pagar:' : 'Precio desde:' }} </h2>
        <span class="text-3xl font-bold text-orange_1" v-if="!isProduct('Hoteles')">${{ calcPrice }}</span>       
         
    </div>
</template>
<script>
import Datepicker from '@vuepic/vue-datepicker';
import '@vuepic/vue-datepicker/dist/main.css'

import moment from 'moment'
export default {    
    name:'HomeRequest',
    props: {
        info: {
            required: true,
        }
    },
    components: {
        Datepicker
    },
    data() {
        return {
            dateRange: {},
            margin: 0,
            price: 0,
            inputVal: 100,
            prod: {}
        }
    },
    created() {
        this.margin = this.info.price
        this.prod = this.info
    },
    watch: {
        price() {this.updRequest()},
        dateRange() {this.updRequest()}
    },    
    computed: {        
        calcPrice() {
            let price  = this.info.price                 
            price = this.inputVal * this.margin/100                      
            return price
        },
        placeholder() {
            let text 
            if(this.isProduct('Hoteles'))
                text = 'Fecha de entrada y salida'
            else if(this.isProduct('Carros'))
                text = 'Fecha de recogida y entrega'
            return text
        }
    },    
    methods: {
        handleDate(range_date) {
            this.dateRange.startDate = moment(range_date[0].toString()).format('DD-MM-YYYY')
            this.dateRange.endDate = moment(range_date[1].toString()).format('DD-MM-YYYY')
        },
        isProduct(prod) {            
            if(this.info.product == prod) {
                this.updRequest()
                return true
            }
            return false
        },
        updRequest() {
            this.price = this.calcPrice
            // let msg = (this.isProduct('Remesas')) ? 'Price a pagar: ' : 'Price desde: '
            
            if(this.price > 0)                
                this.$emit('updInfo', {
                    // price_text: msg,
                    date: `${this.dateRange.startDate} - ${this.dateRange.endDate}`,
                    initprice: this.inputVal,
                    price: this.price,
                })
        }
    }
}
</script>
