<template>
  <div class="bg-white">
    <section class="text-white bg-white">
         <!-- <figure>
            <img src="@/assets/img/singlepole-top-blue.png" alt="" />
         </figure> -->

         <div class="flex flex-col pb-12 gap-y-6 bg-blue_1">
            <h2 class="pt-6 text-3xl text-white md:text-6xl lg:text-4xl">- Enviar solicitud de reserva -</h2>
            <span class="text-5xl font-Clarissa">{{ title }}</span>

            <div class="container p-5 mx-auto border-2 border-white">
              <!-- REQUEST INFO  -->
              
              <general-request :request="request_info" @updInfo="updRequest"></general-request>              
              
              <form class="flex flex-col flex-wrap justify-between flex-1 px-10 py-8 text-xl gap-x-3 gap-y-6 shadow-round bg-dark_blue_1 md:flex-row">                                
                
                <input 
                type="text" 
                placeholder="Nombre completo*" 
                v-model="name" 
                min="0" 
                class="w-full px-6 py-2 text-left border text-orange_1 bg-dark_blue_1 md:w-1/2">

                <input 
                type="text" 
                placeholder="Teléfono*" 
                v-model="phone" 
                min="0" 
                class="w-full px-6 py-2 text-left border text-orange_1 bg-dark_blue_1 md:w-1/5">

                <input 
                type="email" 
                placeholder="Email*" 
                v-model="email" 
                required
                min="0" 
                class="w-full px-6 py-2 text-left border text-orange_1 bg-dark_blue_1 md:w-1/4">

                <div class="flex gap-3 w-full" v-if="request_info.type == 'car'">
                  <input 
                  type="text" 
                  placeholder="Nacionalidad*" 
                  v-model="nationality" 
                  min="0" 
                  class="w-full px-6 py-2 text-left border text-orange_1 bg-dark_blue_1 md:w-1/2">

                  <input 
                  type="text" 
                  placeholder="Pasaporte*" 
                  v-model="passport" 
                  min="0" 
                  class="w-full px-6 py-2 text-left border text-orange_1 bg-dark_blue_1 md:w-1/2">
                </div>

                <textarea id="" 
                placeholder="Descripcion de su solicitud"
                v-model="message" 
                class="w-full h-40 px-8 py-6 text-left border text-orange_1 bg-dark_blue_1"
                ></textarea>

                <div class="flex items-center w-full gap-3">  
                  <span class="items-center inline px-2 py-1 border cursor-pointer bg-dark_blue_1"
                  @click="activeTerm = !activeTerm"
                  :class="isActiveTerm">
                    <i class="fas fa-check"></i>
                  </span>
                  <label class="self-center text-orange_1"
                  @click="activeTerm = !activeTerm"
                  >He leido y acepto los <router-link class="text-light_blue_1" :to="{name: 'TermsAndPolicy'}" target='_blank'>Términos y condiciones</router-link></label> 
                </div>

                <div class="flex flex-col w-full gap-3 md:flex-row">
                  <span class="p-2 px-4 text-left bg-red md:w-3/5" v-show="error">{{ error_message }}</span>
                  <span class="p-2 px-4 text-left bg-green-500 md:w-3/5" v-show="success">La solicitud fue enviada exitosamente!!!</span>
                  <button type="button" class="px-12 py-1 text-base text-white uppercase border md:text-xl bg-orange_1 border-orange_1 md:ml-auto hover:bg-dark_blue_1 hover:text-orange_1"
                    @click="formSubmit"
                    :disabled="buttonDisabled"
                    >
                    <i class="fa fa-spinner fa-pulse text-white" v-show="loading"></i>
                    Enviar Solicitud</button>
                </div>
              </form>
            </div>
         </div>         
    </section>
  </div>
</template>
<script>
import GeneralRequest from '@/components/request/general_request'
import Authorization from "@/services/user/auth.service";

export default {  
  name: "ContactoComp",
  props: {
    title: {
      require: false,
      default: 'Solicitud'
    },    
  },  
  components: {
    GeneralRequest     
  },
  data() {
    return {      
      loading: false,
      activeTerm: false,
      is_hotel: true,   
      success: false,   
      error_message: 'Error. No se pudo enviar la solicitud!!!',   
      error: false,               
      request_info: {type: 'home'},
      name: '',
      phone: '',
      nationality: '',
      passport: '',
      email: '',      
      message: '',
      subject: '',
      msg_info: {},      
      buttonDisabled: false,
    }
  },  
  beforeMount() {
    // iniciando los datos del pedido
    const info = localStorage.getItem('request_info');
    
    if(!info){
      this.$router.push({ name: "Home" });      
      return;
    }

    this.request_info = (info) && JSON.parse(info)
    // this.request = (info) ? JSON.parse(info) : this.request
    if(this.request_info != undefined) {
      this.subject = this.request_info.product      
      }
  },  
  unmounted() {
    localStorage.removeItem('request_info')
  },
  watch: {
    dateRange(d) {
      this.request.date = d.startDate
    }
  },
  computed: {
    isActiveTerm() {
      return (this.activeTerm) ? 'text-orange_1' : 'text-dark_blue_1'
    }
  },
  methods: {
    updPerson(obj){
        this.request.num_person = obj.val
    },

    async formSubmit() {
      this.loading = true;
      this.buttonDisabled = true;
      this.error = false;
      this.validateForm()

      if(this.error) {
        this.buttonDisabled = false;
        this.loading = false;
        return;
      }
            
      let valor = JSON.parse( localStorage.getItem('request_info') );      
    
      if(valor.type === 'hotel' )
        this.sendRequestHotel(valor)
      
      if(valor.type === 'car' ){
        this.validateCar()
        valor.prod = valor.prod.map(car => {
          return {...car, ...this.msg_info};          
        })
        if(this.error) {
          this.buttonDisabled = false;
          return;
        }
        this.sendRequestCar(valor)
      }
      
      this.buttonDisabled = false;
      this.loading = false;

    },

    validateForm(){
  
      if(this.name ==='' || this.phone ==='')
      {
        this.error = true
        let error_message = 'Debe llenar los datos del formulario antes de enviar la solicitud.'
        this.$swal({
            title: 'Error de validación',
            text: error_message,
            icon: 'error',                
            confirmButtonColor: '#3085d6',                            
        })
          this.buttonDisabled = false;
          return;
      }

      if(!this.activeTerm)
      {
        this.error = true        
        let error_message = 'Debe aceptar términos antes de enviar la solicitud.'
        this.$swal({
            title: 'Error de validación',
            text: error_message,
            icon: 'error',                
            confirmButtonColor: '#3085d6',                            
        })
          this.buttonDisabled = false;
          return;
      }

      if(!this.validEmail(this.email))
      {
        this.error = true 
        let error_message = 'Debe insertar una dirección de correo válida.'
        this.$swal({
            title: 'Error de validación',
            text: error_message,
            icon: 'error',                
            confirmButtonColor: '#3085d6',                            
        })
          this.buttonDisabled = false;
          return;
      }


    },
    async sendRequestCar(valor){

      const data = {
        name: this.name,
        phone: this.phone,
        nationality: this.nationality,
        passport: this.passport,
        email: this.email,
        description: this.message,
        destination: valor.destination,
        product: valor.product,      
        car_category: valor.car_category,
        car_engine: valor.car_engine,
        car_pickup_place_id: valor.car_pickup_place_id,        
        car_dropoff_place_id: valor.car_dropoff_place_id,        
        car_pickup_type: valor.car_pickup_type,
        licence: valor.licence, 
        fee: valor.fee,
        price: valor.price,
        prod: valor.prod
      };

      try {
        await this.sendRequest(data)                
      } catch(err) {        
        console.log(err)
      }        
    },
    async sendRequestHotel(valor){
      const data = {
        name: this.name,
        phone: this.phone,
        email: this.email,
        description: this.message,
        destination: valor.destination,
        product: valor.product,
        // date_start: valor.date_start,
        // date_out: valor.date_out,
        num_nights: valor.num_nights,
        price: valor.price,
        prod: valor.prod
      };

      try {
        await this.sendRequest(data)                
      } catch(err) {        
        console.log(err)
      }
              

    },
    async sendRequest(data){
      try {
        await Authorization.checkLogin('/booking/create', {data, auth: 'bearer'}, 'post')        
        localStorage.removeItem('request_info')
        this.success = true 
        this.$swal({
            title: 'Success',
            text: 'Operación es exitosa',
            icon: 'success',                
            confirmButtonColor: '#3085d6',                            
        })
        setTimeout(() => {
            this.$router.push({ name: "Home" });
        }, 5000); 
      } catch(err) {     
        localStorage.removeItem('request_info')   
        this.$swal({
            title: 'Error',
            text: `No se pudo enviar la solicitud`,
            icon: 'error',                
            confirmButtonColor: '#3085d6',                          
        })
        this.error = true
        this.buttonDisabled = false;
        setTimeout(() => {
            this.error = false
            this.$router.push({ name: "Home" });
        }, 8000);          
      }

    },
    validEmail(mail) {
      if(mail) {
        if(mail.match(/^[\w-.]+@([\w-]+\.)+[\w-]{2,8}$/))
          return true
      }
      return false
    },
    validateCar() {      
      if(!this.msg_info.car_pickup_place_id || !this.msg_info.car_dropoff_place_id){  
        this.error = true;      
        this.$swal({
            title: 'Error de validación',
            text: 'Falta seleccionar el lugar!!!',
            icon: 'error',                
            confirmButtonColor: '#3085d6',                            
        })
      }else {      
        return true
      }
      
    },
    updRequest(msg) { 
      this.msg_info = msg
    }
  }
};
</script>
